

let gridImagesOpacity = () => {

  const gridItems = document.querySelector('.grid-images__items')
  const images = document.querySelectorAll('.grid-images__img')
  if (gridItems === null) return

  images.forEach(el => {
    el.addEventListener('mouseover', () => {
      gridItems.classList.add('grid-images__items--on')
      gridItems.classList.remove('grid-images__items--off')
    })
    el.addEventListener('mouseout', () => {
      gridItems.classList.add('grid-images__items--off')
      gridItems.classList.remove('grid-images__items--on')
    })
  });
}

export default gridImagesOpacity