

let isElementInViewport = () => {
  let dataSheetArr = document.querySelectorAll('.data-sheet__item')
  let gridImages = document.querySelectorAll('.grid-images__item')
  let advantagesItems = document.querySelectorAll('.advantages__item')
  let bannerRed = document.querySelector('.banner-red__text')

  let objAnimated = (obj, delay = 0) => {

    window.addEventListener('scroll', () => {

      let animatePoint = Math.round(window.pageYOffset + window.innerHeight/1.2)

      if (animatePoint > obj.offsetTop) {
        
        if (!obj.classList.contains('animated')) {
          setTimeout(() => {
            obj.classList.add('animated')
          }, delay)
        }
        
      }

    })
  } 


  for (var i = 0; i < dataSheetArr.length; i++) {
    objAnimated(dataSheetArr[i], i*50)
  }
  for (var i = 0; i < gridImages.length; i++) {
    objAnimated(gridImages[i], i*100)
  }
  for (var i = 0; i < advantagesItems.length; i++) {
    objAnimated(advantagesItems[i], i*100)
  }
  objAnimated(bannerRed)
  
}

export default isElementInViewport