

let accordionInfo = () => {
  let accordion = document.querySelectorAll('.accordion')
  if (accordion.length === 0) return

  let stateAccordion = (Obj) => {

    let accordionItem = Obj.querySelector('.accordion-item')
    let accordionContent = Obj.querySelector('.accordion-content')
    let accordionBtn = Obj.querySelector('.accordion-btn')
    let stateBtn = false

    accordionBtn.addEventListener('click', () => {
      
      let accordionHeight = accordionContent.clientHeight
      
      if (!stateBtn) {
        stateBtn = true

        if (accordionBtn.classList.contains('minus')) {

          accordionBtn.classList.remove('minus')
          accordionItem.style.height = 0
          accordionItem.style.marginBottom = '0px'
          accordionContent.style.opacity = 0

          accordionItem.addEventListener('transitionend', () => {
            stateBtn = false
          })

        } else {

          accordionBtn.classList.add('minus')
          accordionItem.style.height = accordionHeight+'px'
          accordionItem.style.marginBottom = '100px'
          accordionContent.style.opacity = 1
          
          accordionItem.addEventListener('transitionend', () => {
            stateBtn = false
          })

        }
      }
      
    })
  }

  accordion.forEach(el => {
    stateAccordion(el)
  })
  
}

export default accordionInfo